.DateSelector{display:inline-block;min-width:230px}.DateSelector table td,.DateSelector table th{border:0;padding:0}.DateSelector table tr:hover td,.DateSelector table tr td{background:transparent}.DateSelector div[role=header]{-ms-flex-pack:distribute;display:-webkit-box;display:-ms-flexbox;display:flex;justify-content:space-around;margin-bottom:5px}.DateSelector div[role=header] button{background-color:transparent;border:0!important;color:#000;cursor:pointer;font-size:12.8px;font-size:.8rem}.DateSelector div[role=header] button:disabled{background-color:transparent!important;color:#eee}.DateSelector div[role=header] .MonthSelector{display:inline-block}.DateSelector div[role=header] .MonthSelector select{background:#fff none;background-clip:padding-box;border:1px solid rgba(0,0,0,.15);color:#55595c;font-size:12.8px;font-size:.8rem;height:20px;width:70px}.DateSelector div[role=header] .MonthSelector select:last-child{margin-left:3px}.DateSelector table[role=body]{border-collapse:collapse;margin:auto;width:230px}.DateSelector table[role=body] td,.DateSelector table[role=body] th{font-size:12.8px;font-size:.8rem;height:32px;text-align:center;vertical-align:middle}.DateSelector table[role=body] td:first-of-type,.DateSelector table[role=body] th:first-of-type{color:#fd6162}.DateSelector table[role=body] th{color:#9a999e;font-weight:400}.DateSelector table[role=body] td{color:#515328;cursor:pointer}.DateSelector table[role=body] td:hover{background-color:#f0f1f5;color:#515328}.DateSelector table[role=body] td.after-month,.DateSelector table[role=body] td.before-month{opacity:.2}.DateSelector table[role=body] td.today{background-color:#ebeefa;border:1px solid #2e8bfd;color:#2b8afe;font-weight:700}.DateSelector table[role=body] td.selected{background-color:#2e8bfd;color:#fff;cursor:default}.DateSelector table[role=body] td.disabled{cursor:default;opacity:.2;text-decoration:line-through}